<template>
  <div class="create-container">
    <van-cell-group>
      <van-cell value="" />
      <van-field v-model="title" type="textarea" placeholder="" rows="1" autosize />
    </van-cell-group>
    <van-cell-group>
      <van-cell value="" />
      <van-field v-model="content" type="textarea" placeholder="" rows="1" autosize />
    </van-cell-group>
    <van-cell-group>
      <van-cell value="" />
      <van-cell>
        <div class="add-file" @click="selectImage">
          <i class="iconfont icon-fujian"></i>
        </div>
      </van-cell>
    </van-cell-group>
    <van-cell-group>
      <van-cell title="" is-link :value="formatMeeting" @click="showMeetingTime = true" />
    </van-cell-group>
    <van-cell-group>
      <van-cell value="" />
      <van-cell class="add-container">
        <div class="add-item" v-for="i in 9" :key="i">
          <span><i class="iconfont icon-cha"></i></span>
          <img src="https://bobtestimg.oss-cn-hangzhou.aliyuncs.com/images/1.jpg">
          <div class="name"></div>
        </div>
        <div class="add-item add-man">
          <div class="main" @click="selectJoinMan"><i class="iconfont icon-jia"></i></div>
        </div>
      </van-cell>
    </van-cell-group>
    <div class="bottom-btn">
      <van-button size="large" @click="$router.replace('/notice/releasedetail/100?state=0')"></van-button>
    </div>
    <van-popup v-model="showMeetingTime" position="bottom">
      <van-datetime-picker v-model="meetingDate" :min-date="new Date()" type="datetime" @confirm="meetingConfirm"
        @cancel="meetingCancel" />
    </van-popup>
  </div>
</template>

<script>
  import moment from "moment";

  export default {
    data() {
      return {
        title: "",
        content: "",
        showMeetingTime: false,
        meetingDate: null,
        meetingDone: false
      };
    },
    computed: {
      formatMeeting() {
        if (this.meetingDone) {
          return moment(this.meetingDate).format("YYYY-MM-DD HH:mm");
        } else {
          return "";
        }
      }
    },
    methods: {
      meetingCancel() {
        this.meetingDate = null;
        this.meetingDone = false;
        this.showMeetingTime = false;
      },
      meetingConfirm(value) {
        this.meetingDone = true;
        this.showMeetingTime = false;
        this.meetingDate = value;
      },
      selectJoinMan() {
        window.wx.invoke(
          "selectEnterpriseContact", {
            fromDepartmentId: -1, // -1, 0
            mode: "multi", // singlemulti
            type: ["department", "user"], // departmentuser
            selectedDepartmentIds: ["2", "3"], // IDsingleid
            selectedUserIds: ["lisi", "lisi2"] // IDsingleid
          },
          function (res) {
            if (res.err_msg == "selectEnterpriseContact:ok") {
              if (typeof res.result == "string") {
                res.result = JSON.parse(res.result); //result
              }
            }
          }
        );
      },
      selectImage() {
        window.wx.chooseImage({
          count: 1, // 9
          sizeType: ['original', 'compressed'], // 
          sourceType: ['album', 'camera'], // 
          defaultCameraMode: "batch", //normalbatchnormalbatchnormal
          success: function (res) {
            // var localIds = res.localIds; // ID
            // console.log(res);
            // andriodlocalIdimgsrc
            // IOSgetLocalImgDatabase64img
          }
        });
      }
    },
    beforeCreate() {
      document.title = "";
    }
  };
</script>

<style scoped lang="scss">
  .create-container {
    padding: 30px 0;

    .van-cell-group {
      margin-bottom: 30px;
    }

    .add-file {
      width: 72px;
      height: 72px;
      border: 1px solid #ccc;
      color: #ccc;
      border-radius: 5px;
      text-align: center;
      line-height: 72px;
      @include tapMask;

      .iconfont {
        font-size: 48px;
      }
    }

    .add-container {

      .add-item {
        width: 20%;
        float: left;
        height: 180px;
        padding: 0 20px;
        padding-bottom: 60px;
        position: relative;
        padding-top: 15px;
        margin-bottom: 20px;
        overflow: hidden;

        >span {
          display: block;
          width: 30px;
          height: 30px;
          color: #fff;
          background: #333;
          line-height: 30px;
          text-align: center;
          border-radius: 50%;
          position: absolute;
          right: 5px;
          top: 0;
          @include tapColor;

          i {
            font-size: 16px;
          }
        }

        img {
          width: 100%;
          display: block;
          border-radius: 15px;
        }

        .name {
          width: 100%;
          position: absolute;
          left: 0;
          right: 0;
          bottom: 0;
          line-height: 60px;
          text-align: center;
          @include textof;
        }

        &.add-man {
          .main {
            width: 100%;
            padding-top: 100%;
            position: relative;
          }

          i {
            top: 0;
            left: 0;
            border: 1px solid #ccc;
            display: block;
            width: 100%;
            height: 100%;
            color: #ccc;
            font-size: 56px;
            line-height: 100px;
            text-align: center;
            border-radius: 15px;
            @include tapMask;
            position: absolute;
            overflow: hidden;
          }
        }
      }
    }

    .bottom-btn {
      padding: 0 30px;

      .van-button {
        background: #2f7dcd;
        color: #fff;
        border-radius: 15px;
      }
    }
  }
</style>