<template>
  <div class="submit-container">
    <van-cell-group>
      <van-cell value="" />
      <van-cell>
        {{pd.title}}
      </van-cell>
    </van-cell-group>
    <van-cell-group>
      <van-cell value="" />
      <van-cell>
        {{pd.content}}
      </van-cell>
    </van-cell-group>
    <van-cell-group>
      <van-cell>
        <template slot="title">
          
          <div class="text-tip"></div>
        </template>
      </van-cell>
      <van-cell class="file-container">
        <div class="file-item" v-for="(i,index) in images" :key="index" @click="viewImage(i)">
          <div class="main">
            <img :src="i">
          </div>
        </div>
      </van-cell>
    </van-cell-group>
    <van-cell-group>
      <van-cell title="" :value="pd.startTime|getTime|fTime('YYYY-MM-DD HH:mm')" />
      <van-cell title="" :value="pd.endTime|getTime|fTime('YYYY-MM-DD HH:mm')" />
    </van-cell-group>
    <van-cell-group>
      <van-cell value="" />
      <van-field v-model="content" type="textarea" placeholder="" rows="1" autosize />
    </van-cell-group>
    <van-cell-group>
      <van-cell value="" />
      <van-cell class="submitfile-container">
        <div class="file-item" v-for="(i,index) in submitImages" :key="index" @click="viewImage(i)">
          <div class="main">
            <i class="iconfont icon-cha" @click.stop="submitImages.splice(index, 1)"></i>
            <img :src="i">
          </div>
        </div>
        <div class="file-item add-file" @click="selectImage">
          <div class="main">
            <i class="iconfont icon-fujian"></i>
          </div>
        </div>
      </van-cell>
    </van-cell-group>

    <div class="bottom-btn">
      <van-button size="large" @click="$router.replace('/supervise/myreply')"></van-button>
    </div>
  </div>
</template>

<script>
  import {
    Toast
  } from "vant";
  export default {
    data() {
      return {
        content: '',
        pd: null,
        submitImages: []
      }
    },
    beforeCreate() {
      document.title = ''
    },
    computed: {
      id() {
        return this.$route.params.id
      },
      images() {
        if (this.pd) {
          return this.pd.attachment.split('|')
        } else {
          return []
        }
      }
    },
    mounted() {
      let that = this
      that.$http.get('/api/WorkNotify/ReplyDetail2', {
        params: {
          id: that.id
        }
      }).then(r => {
        console.log(r);
        that.pd = r
      })
    },
    methods: {
      viewImage(url) {
        let that = this
        window.wx.previewImage({
          current: url, // http
          urls: that.submitImages // http
        });
      },
      selectImage() {
        let that = this;
        window.wx.chooseImage({
          count: 1, // 9
          sizeType: ["original", "compressed"], // 
          sourceType: ["album", "camera"], // 
          defaultCameraMode: "batch", //normalbatchnormalbatchnormal
          success: function (selectres) {
            // var localIds = res.localIds; // ID
            console.log(selectres);
            // andriodlocalIdimgsrc
            // IOSgetLocalImgDatabase64img
            window.wx.uploadImage({
              localId: selectres.localIds[0], // IDchooseImage
              isShowProgressTips: 1, // 1
              success: function (res) {
                var serverId = res.serverId; // ID
                console.log(serverId);
                Toast.loading('')
                that.$http.get("/api/file/upload?media=" + serverId).then(r => {
                  that.submitImages.push('http://wibgchina.cnvp.com.cn' + r.replace('\\', '/'))
                  Toast.clear()
                });
              },
              fail(err) {
                console.log(err);
              }
            });
          }
        });
      }
    }
  }
</script>

<style scoped lang="scss">
  .submit-container {
    padding: 30px 0;

    .van-cell-group {
      margin-bottom: 30px;
    }


    .file-container {
      padding-top: 20px;
      padding-bottom: 0;

      @include clearfix;

      .file-item {
        width: 20%;
        float: left;
        border-radius: 5px;
        padding: 10px;

        .main {
          width: 100%;
          padding-top: 100%;
          @include tapMask;
          overflow: hidden;
        }

        img {
          display: block;
          width: 100%;
          height: 100%;
          border-radius: 15px;
          position: absolute;
          top: 0;
          left: 0;
          object-fit: cover;
        }
      }
    }

    .text-tip {
      font-size: 20px;
      color: #999;

      position: absolute;
      right: 30px;
      top: 50%;
      transform: translateY(-50%);
    }

    .bottom-btn {
      padding: 0 30px;

      .van-button {
        background: #2f7dcd;
        color: #fff;
        border-radius: 15px;
      }
    }

    .submitfile-container {
      padding-top: 20px;
      padding-bottom: 0;

      @include clearfix;

      .file-item {
        width: 20%;
        float: left;
        border-radius: 5px;
        padding: 10px;

        &.add-file {
          .main {
            border: 1px solid #ccc;
            border-radius: 15px;
          }

          .iconfont {
            font-size: 64px;
            position: absolute;
            top: 50%;
            left: 50%;
            color: #ccc;
            transform: translate(-50%, -50%)
          }
        }

        .main {
          padding-top: 100%;
          width: 100%;
          @include tapMask;
          overflow: hidden;

          .icon-cha {
            display: block;
            width: 50px;
            height: 50px;
            background: rgba(0, 0, 0, .5);
            color: #fff;
            text-align: center;
            line-height: 50px;
            position: absolute;
            top: 0;
            right: 0;
            font-size: 24px;
            z-index: 100;
            border-radius: 0 15px 0 15px;
            @include tapColor;
          }
        }

        img {
          display: block;
          width: 100%;
          height: 100%;
          border-radius: 15px;
          position: absolute;
          top: 0;
          left: 0;
          object-fit: cover;
        }
      }
    }
  }
</style>