import Vue from 'vue'
import App from './App.vue'
import router from "./router";
import axios from 'axios'
import VueAxios from 'vue-axios'
import "./assets/util/vconsole";

import Vant from 'vant';
import 'vant/lib/vant-css/index.css';

import moment from "moment";

Vue.use(Vant);
Vue.use(VueAxios, axios)
Vue.config.productionTip = false

let wx = window.wx
axios.interceptors.response.use(function (res) {
  // 
  // removePending(res.config);
  if (res.data.IsSuccess) {
    return res.data.Data
  } else {
    console.log(res.data.Message);
    return false
  }
}, function (error) {
  // 
  return Promise.reject(error);
});

Vue.filter('fTime', function (value, format) {
  if (!value) return ''
  if (!format) return moment(value.length === 10 ? parseInt(value) * 1000 : parseInt(value)).format('YYYY-MM-DD HH:mm:ss')
  return moment(value.length === 10 ? parseInt(value) * 1000 : parseInt(value)).format(format)
})

Vue.filter('getTime', function (value) {
  if (!value) return ''
  let len = value.length
  return value.substring(6,len-2)
})

if (/micromessenger/.test(navigator.userAgent.toLowerCase())) {
  axios.get('/api/ticket/get').then(r => {
    if (r) {
      wx.config({
        beta: true, // wx.invokejsapi
        debug: false, // ,apialertpclogpc
        appId: 'wwb380417e702d20f6', // corpID
        nonceStr: r.nonceStr,
        signature: r.signature,
        timestamp: r.timestamp,
        jsApiList: ['selectEnterpriseContact', 'previewImage', 'uploadImage', 'chooseImage'] // JSJS2
      });

      wx.ready(function () {
        new Vue({
          router,
          render: h => h(App),
        }).$mount('#app')
      })

      wx.error(function (res) {
        console.log(res);
      });
    }
  })
} else {
  new Vue({
    router,
    render: h => h(App),
  }).$mount('#app')
}